$(document).ready(function() {
    AOS.init();
    
    $('#home .container .col .lin').click(function(){
        $('#home .container .col').removeClass('active');
        //$('#home .container .row').removeClass('clicked');
        $('#home .container .row').addClass('clicked');
        $(this).closest('.col').addClass('active');
    });

    $('#home .container .col a.close').click(function(){
        $('#home .container .col').removeClass('active');
        $('#home .container .row').removeClass('clicked');
    });


    $('#head a.mobileNav').click(function(){
        if($('.dropdown').hasClass('active')){
            $('a.mobileNav').removeClass('active');
            $('a.mobileNav').find('i').addClass('icon-menu').removeClass('icon-close');
            $('.dropdown').removeClass('showed');
            setTimeout(
                function(){
                    $('.dropdown').removeClass('active');
                }, 100);
            setTimeout(
                function(){
                    $('#head').removeClass('open');
            }, 800);
        }else{
            $('#head').addClass('open');
            $('.dropdown').addClass('active');
            $('a.mobileNav').addClass('active');
            $('a.mobileNav').find('i').removeClass('icon-menu').addClass('icon-close');
            setTimeout(
                function(){
                    $('.dropdown').addClass('showed');
                }, 100);
        }
    });

   $('.jsLink').click(function(){
       window.location=$(this).find("a").attr("href");
       return false;
   });

});
